.project{
    position: relative;
}
.project h3{
    text-align: center;
    font-weight: 600;
}
.project .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  margin: auto;
  opacity: 0;
  -webkit-transition: .5s ease;
  transition: .5s ease;
  background-color: #1f2139;
}
.project:hover .overlay {
  opacity: 0.8;
}
.project .text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}
.project .text i{
    font-size: 4em;
}

